
.footer-cont {
    width: 100vw;
    min-height: 20rem;
    background-color: #1a1a1f;
    position: relative;
    z-index: 2;
    opacity: .8;
}
.footer-cont .logo {
    width: 6em;
    transform: translateX(-8px);
}
.footer-cont .social-cont svg{
    margin-right: 1.5em;
    margin-top: 1em;
}

.footer-cont .motto {
    color: #73747f;
    font-size: .8rem;
}

.footer-cont .info {
    font-size: .8rem;
    color: #73747f;
    letter-spacing: 1px;
}
.footer-cont .copy-right {
    transform: translateY(4em);
    font-size: .65rem;
    letter-spacing: 1px;
}

@media (max-width: 991.98px) {
    .footer-cont { width: 100%; }   
}