:root {
    --primary: #00FF19;
}


.project-tab-cont {
    width: 13em;
    max-height: 20em;
    background-color: #262629;
}
.project-tab-cont .project-tab-img {
    width: 100%;
    height: 100%;
}
.project-tab-cont .project-tab-detail {
    padding: .7em;
}
.project-tab-cont h6 {
    color: #fff;
    font-weight: 800;
}
.project-tab-cont p {
    font-size: .75rem;
    padding: .5em 0;
    color: #7B7B7B;
    line-height: 1.2em;
}
.project-tab-img-cont {
    width: 100%;
    height: 8em;
    background-color: #515151;
    animation: glow 2s infinite;
}
@keyframes glow {
    0% {
        background-color: #515151;
    }
    50% {
        background-color: #434343;
    }
    100% {
        background-color: #515151;
    }
}