:root {
    --primary: #00FF19;
    --var: #7f8d89;
}

.contact-me-cont .map-cont {
    height: 94vh;
    width: 49.5%;
    position: fixed;
    top: 3em;
}
.contact-me-cont .to-do {
    color: #73747f;
    background-color: #202024;
    height: 14em;
    writing-mode: vertical-rl;
    z-index: 1;
    position: absolute;
    left: 0;
    top: 4em;
    font-size: .8rem;
}
.contact-me-cont .to-do .mouse {
    width: 2em;
    height: 2.5em;
    border: 2px solid #464646;
}
.contact-me-cont .to-do .scroll {
    width: 1px;
    height: 1em;
    background-color: var(--primary);
    animation: scroll 1s infinite;
}
@keyframes scroll {
    from { transform: translateY(0); }
    to { transform: translateY(6px); }
}
.contact-me-cont .form-cont {
    min-height: 100vh;
    width: 106%;
    background-color: #313237;
    padding: 6rem 9rem;
    display: flex;
    align-items: center;
}
.contact-me-cont .form-cont input {
    background-color: #313237;
    border: 0;
    border-radius: 0;
    border-bottom: 1px solid #73747f;
    font-size: .85rem;
    padding: 5px 0;
    outline: none;
    color: #73747f;
}
.contact-me-cont .form-cont input:nth-child(1) { width: 47%; }
.contact-me-cont .form-cont input:nth-child(2) { width: 47%; }
.contact-me-cont .form-cont input:nth-child(3) { width: 47%; }
.contact-me-cont .form-cont textarea {
    background-color: #313237;
    border: 1px solid #73747f;
    border-radius: 0;
    min-height: 15em;
    font-size: .85rem;
    padding-top: 14px;
    color: #73747f;
    outline: none;
}
.contact-me-cont .form-cont h4 {
    color: #dddddd;
    font-weight: 900;
    padding: 1em 0;
}
.contact-me-cont .form-cont .stat {
    color: #73747f
}

.contact-me-cont .btn-send {
    text-align: center;
    font-size: .85rem;
    min-height: 3em;
    padding: 0;
    background-color: var(--primary);
    border: 2px solid var(--primary);
    cursor: pointer;
}
.contact-me-cont .btn-send .txt { padding: .45rem .8rem; }
.contact-me-cont .btn-arrow-cont .btn-arrow {
    fill: #fff;
    transition-duration: .2s;
}
.contact-me-cont .btn-arrow-cont {
    background-color: #202024;
    padding: .75rem .8rem;
}
.contact-me-cont .send { width: 10em; }
.contact-me-cont .send:hover .btn-send {
    border: 2px solid #464646;
    background-color: #313237;
    color: var(--primary);
}
.contact-me-cont .send:hover .btn-arrow { transform: translateX(6px); }
.mobile-map-cont { height: 25em; }

@media (max-width: 991.98px) {
    .contact-me-cont .form-cont {
        width: 100%;
        padding: 6rem 1rem;
    }   
}