:root {
    --primary: #00FF19;
}

.btn-primary {
    background-color: var(--primary);
    border: 1px solid var(--primary);
    color: #232327;
    border-radius: 0;
    padding: 0.45rem 0.5rem;
    font-size: .7rem;
    font-weight: 800;
    /* transition-duration: 0s; */
    /* letter-spacing: -1px; */
}
.btn-primary svg {
    fill: #232327;
    margin-left: 7px;
}
.btn-primary:hover {
    background-color: #232327;
    /* color: #fff; */
    /* font-weight: 100; */
    /* letter-spacing: 1px; */
    color: var(--primary);
    border: 1px solid #5d5d5d;
}
.btn-primary:hover .btn-arrow {
    /* transform: translateX(3px); */
    fill: var(--primary);
}


