:root {
    --primary: #00FF19;
}

.about-data-cont {
    width: 120%;
    min-height: 100vh;
    /* background: #e3e3e3; */
    background-color: #313237;;
    padding: 6rem 8rem;
    line-height: 1.4rem;
}

.about-data-cont .stack{
    height: 3rem;
    width: auto;
    margin: 4px;
    cursor: pointer;
    transition-duration: .2s;
    box-shadow: 2px 2px 5px #1e1e22;
    /* border: 1px solid #464646; */
}
.about-data-cont .tag {
    color: var(--primary);
    font-size: .8rem;
}
.coding-img-cont {
    width: 50%;
    display: flex;
    justify-content: center;
}


.about-data-cont .stack:hover {
    transform: scale(1.2);
}
.about-data-cont .coding {
    width: 20em;
}

.about-data-cont p {
    padding-right:  8.5rem;
    color: #a7a7b3;
}
.about-data-cont h2 {
    font-weight: 600;
}
.about-data-cont h6 {
    font-weight: 900;
    color: rgb(165, 165, 165);
}
.about-data-cont h2, p {
    color: #e3e3e3;
}
.about-data-cont ul {
    list-style: none;
}
.about-data-cont ul li {
    padding: 4px 0;
}

.about-data-cont .yt {
    /* background-color: rgb(181, 0, 0);
    color: rgb(208, 208, 208);
    border-radius: 5px;
    padding: 5px;
    font-weight: bold; */
    color: blue;
}
/* .about-data-cont .yt:hover {
    background-color: rgb(143, 0, 0);
} */
.about-data-cont p { 
    font-size: .85rem; 
    letter-spacing: 1px;
    word-spacing: 2px;
}
.about-data-cont .highlight {
    background-color: var(--primary);
    /* background-color: yellowgreen; */
    color: #202024;
    font-weight: 700;
    padding: 4px;
    /* border-radius: 5px; */
}


.about-title-cont {
    width: 45%;
    height: 101vh;
    background-color: #202024;
    position: fixed;
    top: 0;
    right: 0;
}
.about-title-cont > div > .line {
    width: 45px;
    height: 2px;
    background-color: var(--primary);
    transform: translateX(17px);
}
.about-title-cont > div > h2{
    position: relative;
    right: 9rem;
    font-weight: 900;
    color: #fff;
    opacity: .9;
    letter-spacing: 3px;
    /* text-shadow: 1px 1px 0 #787878,
							 1px 2px 0 #787878,
							 1px 3px 0 #787878,
							 1px 4px 0 #787878,
							 1px 5px 0 #787878,
							 1px 6px 0 #787878,
							 1px 9px 0 #000; */
}

.about-title-cont .to-do {
    color: #73747f;
    writing-mode: vertical-rl;
    z-index: 1;
    position: absolute;
    left: 0;
    top: 4em;
    font-size: .8rem;

}
.about-title-cont .to-do .mouse {
    width: 2em;
    height: 2.5em;
    /* background-color: yellow; */
    border: 2px solid #464646;
}
.about-title-cont .to-do .scroll {
    width: 1px;
    height: 1em;
    background-color: var(--primary);
    animation: scroll 1s infinite;
}
@keyframes scroll {
    from { transform: translateY(0); }
    to { transform: translateY(6px); }
}



.about-cont .line-box {
    width: 3em;
    height: 3em;
    position: fixed;
    right: 5em;
    z-index: 1;
}
.about-cont .line-box:nth-child(1) {
    border-right: .6px solid #464646;
    border-top: .6px solid #464646;
    margin-top: 8em;
}
.about-cont .line-box:nth-child(2) {
    border-right: .6px solid #464646;
    border-bottom: .6px solid #464646;
    bottom: 0;
    margin-bottom: 6em;
}


@media screen and (max-width: 991.98px) {
    .about-data-cont {
        width: 100%;
        padding: 6rem 1.5rem;
    }   
}