:root {
    --primary: #2d2d2d;
}

body {
    background:  url('../img/bg\ img.png') #26262a center center/cover;
    /* overflow: hidden; */
}
a {text-decoration: none;}

.stroke {
    width: .5px;
    height: 93.5%;
    background: #464646;
    position: absolute;
    top: 6.5%;
}

.home-cont {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}
.home-cont .data-cont {
    width: 68vw;    
    padding-left: 10rem;
    margin-left: 120vw;
    animation: slideData .9s ease-out forwards .8s;
}
@keyframes slideData {
    from { margin-left: 120vw; }
    to { margin-left: 0; }
}
.home-cont .data-cont h1 {
    font-weight: 900;
    color: var(--primary);
    text-shadow: 1px 1px 0 #004f08,
							 1px 2px 0 #004f08,
							 1px 3px 0 #004f08,
							 1px 4px 0 #004f08,
							 1px 5px 0 #004f08,
							 1px 6px 0 #004f08,
							 1px 7px 0 #004f08,
							 1px 8px 0 #004f08,
							 1px 11px 0 rgb(27, 27, 27);
}

.home-cont .data-cont h1 > span {
    color: #464646;
    text-shadow: 1px 1px 0 #2d2d2d,
							 1px 2px 0 #2d2d2d,
							 1px 3px 0 #2d2d2d,
							 1px 4px 0 #2d2d2d,
							 1px 5px 0 #2d2d2d,
							 1px 6px 0 #2d2d2d,
							 1px 7px 0 #2d2d2d,
							 1px 8px 0 #2d2d2d,
							 1px 11px 0 rgb(27, 27, 27);
}
.home-cont .data-cont .motto {
    color: #757474;
    font-size: .6em;
    font-weight: 800;
    margin-left: -4em;
    margin-top: -1em;
    writing-mode: vertical-rl;
    word-spacing: 2px;
    position: absolute;
}
.home-cont .data-cont .pro {
    color: #757474;
    font-size: 1.2rem;
    font-weight: 800;
}
.home-cont .learn-btn {
    color: #fff;
    font-size: .7rem;
    font-weight: 600;
    letter-spacing: 1px;
    cursor: pointer;
}
.home-cont .line {
    width: 2.5rem;
    height: 2px;
    position: relative;
    top: -3.5em;
    background-color: var(--primary);
}
.arrow {
    transition-duration: .1s;
}
.learn-btn:hover .arrow {
    transform: translateX(6px);
}

.stroke-cont .line-box {
    width: 3em;
    height: 3em;
    margin-right: 85%;
    position: absolute;
}
.stroke-cont .line-box:nth-child(1) {
    border-top: .6px solid #464646;
    border-left: .6px solid #464646;
    top: 12%;
}
.stroke-cont .line-box:nth-child(2) {
    bottom: 7%;
    border-bottom: .6px solid #464646;
    border-left: .6px solid #464646;
}

@media (max-width: 991.98px) {
    .home-cont .data-cont { 
        /* text-align: center;  */
    }
    .home-cont .data-cont h1 {
        font-size: 1.8rem;
    }
    .home-cont .data-cont {
        width: 100vw;    
        padding-left: 4em;
    }
    .stroke {
        /* width: .5px; */
        height: 95%;
        /* background: #464646; */
        /* position: absolute; */
        top: 6.5%;
    }
}