:root {
    --sbar-color1: #2D2D32;
    --sbar-color2: #39393e;
    --primary: #00FF19;
    --width: 5em;
    --nav-font: .65rem;
    --social-color: #565656;
}

.sbar-bg {
    width: var(--width);
    height: 101vh;
    background-color: var(--sbar-color1);
    position: fixed;
    top: 0;
    z-index: 7;
    opacity: .4;
    box-shadow: -9px 0 20px #000;
}

.sbar-cont {
    width: var(--width);
    height: 101vh;
    position: fixed;
    top: 0;
    z-index: 8;
}

.sbar-text-cont {
    width: 26rem;
    font-size: var(--nav-font);
    display: flex;
    align-items: center;
    transform: rotate(90deg);
    transform-origin: left;
    position: relative;
    left: 2.55rem;
    top: -.4rem;
}
.sbar-text-cont p {
    letter-spacing: 2px;
    color: #ababab;
    font-size: .55rem;
}
.sbar-div {
    background-color: #3c3c40;
    width: 7rem;
    height: 1.8rem;
    position: relative;
    left: 2rem;
    z-index: -1;
}
.sbar-cont .line {
    width: 20px;
    height: 1px;
    background-color: var(--primary);   
    transform: rotate(90deg);
}
.socials-cont {
    transform: rotate(-90deg);
    position: relative;
    left: 5rem;
}
.sbar-cont .socials-cont div {
    width: 3em;
    height: 3em;
    border-left: .4px solid var(--social-color);
    border-right: .4px solid var(--social-color);
    border-bottom: .4px solid var(--social-color);
    display: flex;
    justify-content: center;
    align-items: center;
}
.sbar-cont .socials-cont div:nth-child(1) {
    border-top: .4px solid var(--social-color);
}