:root {
    --primary: #00FF19;
}

.project-data-cont {
    width: 120%;
    min-height: 100vh;
    background-color: #313237;
    padding: 6rem 8rem;
    line-height: 1.4rem;
}

.project-data-cont .search-bar {
    outline: none;
    background-color: #313237;
    color: #73747f;
    border: none;
    border-bottom: 2px solid #5a5a5a;
    padding: .4rem .7rem;
    font-size: .85rem;
    width: 20em;
}

.project-title-cont {
    width: 45%;
    height: 101vh;
    background-color: #202024;
    position: fixed;
    top: 0;
    right: 0;
}

.project-title-cont > div > .line {
    width: 45px;
    height: 2px;
    background-color: var(--primary);
    transform: translateX(17px);
}
.project-title-cont > div > h2{
    position: relative;
    right: 9rem;
    font-weight: 900;
    color: #fff;
    opacity: .9;
    letter-spacing: 3px;
}
.project-cont .line-box {
    width: 3em;
    height: 3em;
    position: fixed;
    right: 6em;
    z-index: 1;
}
.project-cont .line-box:nth-child(1) {
    border-right: .6px solid #464646;
    border-top: .6px solid #464646;
    margin-top: 8em;
}
.project-cont .line-box:nth-child(2) {
    border-right: .6px solid #464646;
    border-bottom: .6px solid #464646;
    bottom: 0;
    margin-bottom: 6em;
}
.project-cont .to-do {
    color: #73747f;
    writing-mode: vertical-rl;
    z-index: 1;
    position: absolute;
    left: 0;
    top: 4em;
    font-size: .8rem;

}
.project-cont .to-do .mouse {
    width: 2em;
    height: 2.5em;
    border: 2px solid #464646;
}
.project-cont .to-do .scroll {
    width: 1px;
    height: 1em;
    background-color: var(--primary);
    animation: scroll 1s infinite;
}
@keyframes scroll {
    from { transform: translateY(0); }
    to { transform: translateY(6px); }
}

.btn-view-more {
    padding: .4rem .7rem;
    border: 2px solid #494a51;
    border-radius: 0;
    color: var(--primary);
    font-size: .7rem;
}
.btn-view-more:hover {
    border: 2px solid #73747f;
    color: var(--primary);
}

@media (max-width: 991.98px) {
    .project-data-cont {
        width: 100%;
        padding: 6rem 4rem;
    }   
}