:root {
    --primary: #00FF19;
}

.project-overlay {
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: .5;
    position: fixed;
    top: 0;
    z-index: 2;
}

.project-details-cont {
    width: 100%;
    height: 0;
    overflow: hidden;
    background-color: #151517;
    position: fixed;
    bottom: 0;
    z-index: 3;
    /* line-height: 3.5em; */
    line-height: 1.5em;
    padding: 0;
}
.project-details-cont h2 {
    font-weight: 800;
    color: #fff;
}
.project-details-cont p {
    font-size: .9rem;
    color: #999999;
}
.project-details-cont .img-cont { 
    height: 22em;
    width: 32em; 
    background-color: #515151;
    animation: glow 2s infinite;
}
@keyframes glow {
    0% {
        background-color: #515151;
    }
    50% {
        background-color: #434343;
    }
    100% {
        background-color: #515151;
    }
}

.project-details-cont .img-cont img {
    width: 100%;
    height: 100%;
}
.project-details-cont a { margin-right: 20px; margin-top: 5px; }
.project-details-cont .btn-primary {
    background-color: var(--primary);
    border: 1px solid var(--primary);
    color: #151517;
    border-radius: 0;
    padding: 0.6rem 0.9rem;
    font-size: .9rem;
    text-align: center;
}
.project-details-cont .btn-primary svg {
    fill: #151517;
    margin-left: 7px;
}
.project-details-cont .btn-primary:hover {
    background-color: #151517;
    color: var(--primary);
    border: 1px solid #5d5d5d;
}
.project-details-cont .btn-primary:hover .btn-arrow { fill: var(--primary); }


.project-details-cont .btn-secondary {
    background-color: #151517;
    border: 1px solid #5d5d5d;
    color: var(--primary);
    border-radius: 0;
    padding: 0.6rem 0.9rem;
    font-size: .9rem;
    text-align: center;
}
.project-details-cont .btn-secondary svg {
    fill: var(--primary);
    margin-left: 7px;
}
.project-details-cont .btn-secondary:hover { border: 1px solid #999999; }

.project-details-cont .disable {
    opacity: .3; 
    pointer-events: none;
}




.show-project-details {
    height: 0;
    animation: disp .4s ease-out forwards;
}
@keyframes disp {
    0% { 
        height: 0; 
        padding: 0em 8em;
    }
    100% {
        height: 25em;
        padding: 0em 8em;
    }
}
.hide-project-details {
    height: auto;
    animation: hid .4s ease-out forwards;
}
@keyframes hid {
    from { 
        height: 25em; 
        padding: 0em 8em;
    }
    to { 
        height: 0; 
        padding: 0em 8em;
    }
}

@media (max-width: 991.98px) {
    .project-details-cont{
        padding: 1em 0;
    }
    .project-details-cont .img-cont { 
        width: 15em; 
        height: 10em;
    }
    .show-project-details {
        height: 0;
        animation: disp .4s ease-out forwards;
    }
    @keyframes disp {
        0% { 
            height: 0; 
            padding: 0;
        }
        100% {
            height: 25em;
            padding: 0;
        }
    }
    .hide-project-details {
        height: auto;
        animation: hid .4s ease-out forwards;
    }
    @keyframes hid {
        from { 
            height: 25em; 
            padding: 0;
        }
        to { 
            height: 0; 
            padding: 0;
        }
    }
    
}