:root {
    --primary: #00FF19;
    --bg-color: #1e1e22;
    --social-color: #565656;
}
.overlay {
    width: 100%;
    height: 100%;
    background: #0e0e11;
    opacity: .45;
    display: none;
    position: fixed;
    top: 0;
    z-index: 4;
}
.overlay-show {display: block}
.overlay-hide {display: none}
.side-drawer-cont {
    width: 0%;
    height: 100%;
    background-color: var(--bg-color);
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 5;
    overflow: hidden;
}
.side-drawer-show {
    animation: show .3s ease-in forwards;
}
@keyframes show{
    from {
        width: 0%;
    }
    to {
        width: 30%;
    }
}

.side-drawer-hide {
    animation: hide .3s ease-in forwards;
}
@keyframes hide {
    from {
        width: 30%;
    }
    to {
        width: 0%;
    }
}

.info-cont {
    position: absolute;
    bottom: 0;
    background: #2f2f35;
    width: 100%;
    height: 4em;
}
.info-cont p {
    font-size: .7em;
    color: var(--social-color);
    font-weight: 900;
    letter-spacing: 2px;
}
.info-cont div {
    width: 2em;
    height: 2em;
    border-top: .4px solid var(--social-color);
    border-left: .4px solid var(--social-color);
    border-bottom: .4px solid var(--social-color);
    display: flex;
    justify-content: center;
    align-items: center;
}
.info-cont div:nth-child(4) {
    border-right: .4px solid var(--social-color);
}


.menu-cont {
    height: 100%;
    color: #565656;
    font-size: .8rem;
}
.menu-cont ul {
    list-style: none;
    line-height: 2.5rem;
    font-weight: 800;
    letter-spacing: 1px;
}

.menu-cont > div > ul > li > a {
    padding: 0 4px;
    text-decoration: none;
    color: #565656;
    width: 50%;
}
.menu-cont > div > ul > li > div{
    background: #2f2f35;    
    height: 2.5em;
    width: 0;
    position: absolute;
    z-index: -1;
}
.menu-cont > div > ul > li {
    display: flex;
    align-items: center;
    cursor: pointer;
}
.h-effect { transition-duration: .2s; }
.menu-cont > div > ul > li:hover .h-effect{
    width: 30%;
}
.menu-cont > div > ul > li:hover .nav-link{
    color: rgb(215, 215, 215);
}

@media screen and (max-width: 991.98px){ 
    .side-drawer-show {
        animation: show .3s ease-in forwards;
    }
    @keyframes show{
        from {
            width: 0%;
        }
        to {
            width: 55%;
        }
    }
    
    .side-drawer-hide {
        animation: hide .3s ease-in forwards;
    }
    @keyframes hide {
        from {
            width: 55%;
        }
        to {
            width: 0%;
        }
    }
    .menu-cont > div > ul > li > a {
        padding: 0;
        font-size: .6rem;
        width: 100%;
    }   
}


