:root {
    --primary: #00FF19;
    --nav-color1: #2D2D32;
    --nav-color2: #424247;
    --nav-text: hsl(0, 0%, 100%);
    --nav-text-title: #6B6B6B;
    --height: 3em;
    --nav-font: .65rem;
}

.nav-bg {
    width: 100%;
    height: var(--height);
    position: fixed;
    top: 0;
    background-color: var(--nav-color1);
    z-index: 8;
    opacity: .45;
}
.nav-cont {
    width: 100%;
    height: var(--height);
    position: fixed;
    top: 0;
    z-index: 9;
    font-size: var(--nav-font);
}
.logo-cont img{
  width: 9em;
}
.line { 
  width: 20px;
  height: 2px;
  background-color: var(--primary);
}
.sp-line {
  transform: translateX(1rem);
}
.nav-data-cont {
  background-color: #38383E;

}
.nav-data-cont p { 
  letter-spacing: 3px;
  color: #fff;
}
.nav-data-cont span {
  color: #6B6B6B;
}



/*************** HAMBURGER *********************/
.hamburger-cont { height: 100% }
.hamburger-cont p { color: #8E8E8E; cursor: pointer !important;}
.hamburger div{
  background-color: var(--primary);
  width: 1.8rem;
  height: 2px;
  margin-top: 6px;
}
.hamburger div:nth-child(2) { width: 1rem;}

/* .hamburger-line2{ transition-duration: 1s !important;} */
.hamburger:hover .hamburger-line2{ width: 1.8rem !important; }
/* .cross-to-hamburger:hover .hamburger-line2{ width: 1.8rem !important; } */

.hamburger-to-cross{ cursor: pointer; }
.hamburger-to-cross div{
  background-color: var(--primary);
  width: 1.8rem;
  height: 2px;
  margin-top: 6px;
}
.hamburger-to-cross div:nth-child(2) { width: 1.4rem; }
.hamburger-to-cross div:nth-child(1) {
  animation: change1 .2s ease-out forwards;
}
@keyframes change1 {
  from {
    width: 1.8rem;
    transform: rotate(0deg);
  }
  to {
    width: 1.6rem;
    transform: translateY(.3rem) rotate(40deg);
  }
}
.hamburger-to-cross div:nth-child(2) {
  animation: change2 .2s ease-out forwards .1s;
}
@keyframes change2 {
  from {
    transform: rotate(0deg);
  }
  to {
    width: 1.6rem;
    transform: translateY(-.2rem) rotate(-40deg);
  }
}
.cross-to-hamburger { cursor: pointer; }
.cross-to-hamburger div:nth-child(1) {
  animation: rev1 .2s ease-out forwards;
}
@keyframes rev1 {
  from {
    width: 1.6rem;
    transform: translateY(.3rem) rotate(40deg);
  }
  to {
    width: 1.8rem;
    transform: rotate(0deg);
  }
}

.cross-to-hamburger div:nth-child(2) {
  animation: rev2 .2s ease-out forwards;
}
@keyframes rev2 {
  from {
    width: 1.6rem;
    transform: translateY(-.2rem) rotate(-40deg);
  }
  to {
    width: 1rem;
    transform: rotate(0deg);
  }
}

/*******************************************/

@media (max-width: 991.98px) {
  .nav-cont { padding: 0 1em; }
}